import { NavBar } from "./components/navbar/NavBar";
import Highlight from "react-highlight";

import "./App.css";
import Particles from "react-particles-js";

function App() {
  return (
    <div style={{ height: "100vh", overflow: "hidden" }}>
      <div style={{ position: "absolute", overflow: "hidden" }}>
        <Particles
          style={{ zIndex: 2 }}
          width="100vw"
          height="100vh"
          params={{
            particles: {
              move: { speed: 1 },
              color: {
                value: "#333333",
              },
              line_linked: {
                color: {
                  value: "#333333",
                },
              },
              number: {
                value: 60,
              },
              size: {
                value: 2,
              },
            },
            interactivity: {
              events: {
                onhover: {
                  enable: true,
                  mode: "repulse",
                },
              },
            },
          }}
        />
      </div>
      <div
        style={{
          display: "flex",
          flexGrow: 1,
          height: "100%",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <div style={{ border: "1px solid red" }}>
          <h1>Frontend Dev</h1>
        </div>
        <div style={{ border: "1px solid red" }}>
          <Highlight language="javascript">
            {`function foo() { return 'bar' }`}
          </Highlight>
        </div>
      </div>
    </div>
  );
}

export default App;
